import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { AlertBar } from 'ng2-alert-bar';

@Component({
  selector: 'app-redefinir-senha',
  templateUrl: './redefinir-senha.component.html',
  styleUrls: ['./redefinir-senha.component.css']
})
export class RedefinirSenhaComponent implements OnInit {

  @ViewChild('password') inputPassword: ElementRef;
  @ViewChild('confirmPassword') inputPasswordConfirm: ElementRef;

  public formulario: FormGroup;
  private isValidToken: Boolean = false;
  private email: string = "";
  private token: string = "";
  public loading: Boolean = false;

  constructor(private formBuilder: FormBuilder,
              private route: Router,
              private routeActive: ActivatedRoute,
              private http: HttpClient,
              private alert: AlertBar) { }

  ngOnInit() {
    this.defineForm();
    this.email = this.routeActive.snapshot.params['email'];
    this.token = this.routeActive.snapshot.params['token'];
    this.verifyToken();
  }

  /**
   * Define os campos e validadores do formulário
   */
  private defineForm() {
    this.formulario = this.formBuilder.group({
      password: [ null, Validators.required ],
      confirmPassword: [ null, Validators.required ]
    });
  }

  /**
   * Verifica os dados do formulário e caso válidos redireciona para
   * pagina de login.
   */
  public recoverUpdate() {
    // Visualização dos dados do formulário no console
    // @ this.formulario.value = Dados do formulário
    console.log(JSON.stringify( this.formulario.value ) );

    if (this.formulario.valid) {
      if (this.isValidToken) {
        this.loading = true;
        let params = {
            email: this.email,
            token: this.token,
            password: this.formulario.get('password').value,
            confirmarPassword: this.formulario.get('confirmPassword').value
        };
        console.log("parans", params);
        this.http.post('api/auth/password/update', params).subscribe(
          data => {
            this.loading = false;
            this.alert.success("", JSON.stringify(data));
            this.route.navigate(['/login']);
          },
          error => {
            this.loading = false;
            if (error.status === 422) {
              for (let msg in error.error.messages) {
                return this.alert.error("Ops :)", error.error.messages[msg]);
              }
            }
            this.alert.error("Ops :)", error.error);
          }
        );
      } else {
        this.alert.error("Ops :)", "Não foi possível atualizar sua senha. Realize novamente o procedimento de recuperação de senha.");
      }
    } else {
      this.alert.error("Ops :)", "Senhas inválidas! Digite-as novamente.");
    }
  }

  private verifyToken() {
    this.loading = true;
    let params = {
      email: this.email,
      token: this.token
    }
    this.http.get('api/auth/confirm-token/verify', { params: params }).subscribe(
      data => {
        this.loading = false;
        this.isValidToken = true;
      },
      error => {
        this.loading = false;
        this.isValidToken = false;
        if (error.status === 422) {
          for (let msg in error.error.messages) {
            return this.alert.error("Ops :)", error.error.messages[msg]);
          }
        }
        this.alert.error("Ops :)", error.error);
      }
    );
  }
}



