// Angular Imports
import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

// Component
import { DatePickerComponent } from './components/date-picker/date-picker.component';

import { AuthGuard } from './guards/index';

// Screens Components
import { AccessComponent } from './screens/access/access.component';
import { CadastrarComponent } from './screens/cadastrar/cadastrar.component';
import { PerfilComponent } from './screens/perfil/perfil.component';
import { RecuperarComponent } from './screens/recuperar/recuperar.component';
import { RecuperarFeitoComponent } from './screens/recuperar-feito/recuperar-feito.component';
import { RecursosHumanosComponent } from './screens/recursos-humanos/recursos-humanos.component';
import { SagaComponent } from './screens/saga/saga.component';
import { MarketingComponent } from './screens/marketing/marketing.component';
import { ComprasComponent } from './screens/compras/compras.component';
import { ServicosComponent } from './screens/servicos/servicos.component';
import { JogosComponent } from './screens/jogos/jogos.component';
import { FinanceiroComponent } from './screens/financeiro/financeiro.component';
import { FinalComponent } from './screens/final/final.component';
import { DashboardComponent } from './screens/dashboard/dashboard.component';
import { RedefinirComponent } from './screens/redefinir/redefinir.component';

const APP_ROUTES: Routes = [
    { path: '', redirectTo: 'dashboard', pathMatch: 'full' },
    { path: 'login', component: AccessComponent },
    { path: 'cadastrar/:email/:token', component: CadastrarComponent },
    { path: 'recuperar', component: RecuperarComponent },
    { path: 'recuperar-feito', component: RecuperarFeitoComponent },
    { path: 'redefinir/:email/:token', component: RedefinirComponent },
    // rotas protegidas...
    { path: 'perfil/:codigoJogo', component: PerfilComponent, canActivate: [AuthGuard] },
    { path: 'recursos-humanos', component: RecursosHumanosComponent, canActivate: [AuthGuard] },
    { path: 'saga', component: SagaComponent, canActivate: [AuthGuard] },
    { path: 'saga/:state', component: SagaComponent, canActivate: [AuthGuard] },
    { path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard] },
    { path: 'marketing', component: MarketingComponent, canActivate: [AuthGuard] },
    { path: 'compras', component: ComprasComponent, canActivate: [AuthGuard]},
    { path: 'financeiro', component: FinanceiroComponent, canActivate: [AuthGuard] },
    { path: 'servicos', component: ServicosComponent, canActivate: [AuthGuard] },
    { path: 'jogos', component: JogosComponent, canActivate: [AuthGuard] },
    { path: 'final', component: FinalComponent, canActivate: [AuthGuard] },
    { path: '**', component: AccessComponent }
];

export const routing: ModuleWithProviders = RouterModule.forRoot(APP_ROUTES);



