import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-simple-perfil',
  templateUrl: './simple-perfil.component.html',
  styleUrls: ['./simple-perfil.component.css']
})
export class SimplePerfilComponent implements OnInit {

  @Input('isDefinable') isDefinable: boolean;
  @Input('name') name: string;
  @Input('avatar') avatar: string;
  @Input('works') works: Array<any>;
  @Input('job') job: string;
  @Input('refForm') refForm: any;

  private initials: string;
  private settings = {};

  constructor() { }

  ngOnInit() {
    this.ifDontHaveAvatar();

    this.settings = {
      text: "Selecione os cargos",
      selectAllText: 'Marcar todos',
      unSelectAllText: 'Desmarcar todos'//,
      //classes: "myclass field field--select"
    };
  }

  /**
   * Caso não houver avatar a inicial do nome do
   * profissional é colocada no lugar do mesmo.
   */
  private ifDontHaveAvatar() {
    const arrayName = this.name.split(' ').map(String);
    const sg = arrayName[0].substring(0, 1) + arrayName[1].substring(0, 1);
    this.initials = sg;
  }

  /**
   * Visualiza o base64 gerado após o upload da imagem
   * @param  $event Objeto recebido após upload da imagem
   */
  private seeUpload($event) {
    console.log($event.base64);
  }

}



