import { Component, OnInit } from '@angular/core';

// Plugins
import { NgxCarousel } from 'ngx-carousel';

@Component({
  selector: 'app-financeiro-slider-card',
  templateUrl: './financeiro-slider-card.component.html',
  styleUrls: ['./financeiro-slider-card.component.css']
})
export class FinanceiroSliderCardComponent implements OnInit {

  public carouselLoan: NgxCarousel;

  public loans: Array<any> = [
    {
      loan_value: 20000,
      total_payment: 8000,
      partials: [
        {date: '05/12', price: '4000', status: 'Pago'},
        {date: '05/01', price: '4000', status: 'Pago'},
        {date: '05/02', price: '4000', status: 'Em aberto'},
        {date: '05/03', price: '4000', status: 'Em aberto'},
        {date: '05/04', price: '4000', status: 'Em aberto'}
      ]
    },
    {
      loan_value: 12000,
      total_payment: 8000,
      partials: [
        {date: '05/12', price: '4000', status: 'Pago'},
        {date: '05/01', price: '4000', status: 'Pago'},
        {date: '05/02', price: '4000', status: 'Em aberto'}
      ]
    },
  ];

  constructor() { }

  ngOnInit() {
    this.startCarouselProperty();
  }

  /**
   * Inicializa o carrossel docs: https://www.npmjs.com/package/ngx-carousel
   */
  public startCarouselProperty() {
    this.carouselLoan = {
      grid: {xs: 1, sm: 1, md: 1, lg: 1, all: 0},
      slide: 1,
      speed: 400,
      interval: 4000,
      point: {
        visible: false
      },
      load: 2,
      touch: true,
      loop: true,
      custom: 'banner'
    };
  }

  /**
   * Verifica o id do slide atual, método emitido
   * toda vez que o slide muda para o próximo
   * @param  $event Id do slide ativo no momento
   */
  public carouselChange($event) {
    // console.log($event);
  }

}



