import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-dashboard-compras',
  templateUrl: './dashboard-compras.component.html',
  styleUrls: ['./dashboard-compras.component.css']
})
export class DashboardComprasComponent implements OnInit {

  public responsible = 'Ana Maria';
  public arrayShop: Array<any> = [
    {
      id: 0,
      product_name: 'Post-it',
      thumbnail: 'js/assets/icons/icon-papers-line.svg',
      actualQuant: 120,
      modalOpen: false,
      status: 'success',
      providers: [
        {
          id: 0,
          name: 'Papelaria A',
          logo: 'js/assets/icons/icon-store.svg',
          unit_value_product: 0.50,
          send_time: 23 // days
        },
        {
          id: 1,
          name: 'Papelaria B',
          logo: 'js/assets/icons/icon-store.svg',
          unit_value_product: 0.70,
          send_time: 17 // days
        }
      ]
    }, {
      id: 1,
      product_name: 'Papel',
      thumbnail: 'js/assets/icons/icon-papers-line.svg',
      actualQuant: 8,
      modalOpen: false,
      status: 'danger',
      providers: [
        {
          id: 2,
          name: 'Papelaria A',
          logo: 'js/assets/icons/icon-store.svg',
          unit_value_product: 0.50,
          send_time: 23 // days
        },
        {
          id: 3,
          name: 'Papelaria B',
          logo: 'js/assets/icons/icon-store.svg',
          unit_value_product: 0.70,
          send_time: 17 // days
        }
      ]
    }, {
      id: 2,
      product_name: 'Anotações',
      thumbnail: 'js/assets/icons/icon-papers-line.svg',
      actualQuant: 12,
      modalOpen: false,
      status: 'danger',
      providers: [
        {
          id: 4,
          name: 'Papelaria A',
          logo: 'js/assets/icons/icon-store.svg',
          unit_value_product: 0.30,
          send_time: 13 // days
        },
        {
          id: 5,
          name: 'Papelaria B',
          logo: 'js/assets/icons/icon-store.svg',
          unit_value_product: 0.20,
          send_time: 10 // days
        }
      ]
    },
  ];

  constructor() { }

  ngOnInit() {
  }


  /**
   * Evento que define a variável de controle do modal como 'false'
   * @param  $event Objeto recebido sempre que o modal é fechado
   * @param idProduct Id do produto que teve seu modal fechado
   */
  public closedModal($event: any, idProduct: number) {
    if ( $event.state === 'close' ) {
      for ( const item of this.arrayShop ) {
        if ( item.id === idProduct ) {
          item.modalOpen = false;
          return;
        }
      }
    }
  }

  /**
   * Evento que define a variável de controle do modal como 'true'
   * @param idProduct Id do modal a ser aberto
   */
  public openModal(idProduct: number) {
    for ( const item of this.arrayShop ) {
      if ( item.id === idProduct ) {
        item.modalOpen = true;
        return;
      }
    }
  }

}



