import { Component, OnInit, Input, ViewChild, ElementRef } from '@angular/core';

@Component({
  selector: 'app-saga-button',
  templateUrl: './saga-button.component.html',
  styleUrls: ['./saga-button.component.css']
})
export class SagaButtonComponent implements OnInit {

  @Input('goTo') goTo = '';
  @Input('bare') bare = false;
  @Input('label') label = '';
  @Input('inverse') inverse = false;
  @Input('size') size: string;

  @ViewChild('link') linkButton: ElementRef;
  @ViewChild('button') trueButton: ElementRef;

  public labelToButton = '';

  constructor() { }

  ngOnInit() {
    this.setText();
    this.setTypeButton();
  }

  /**
   * Define o texto do botão
   */
  public setText() {
    if ( this.label !== '' ) {
      this.labelToButton = this.label;
    } else {
      console.log( 'O component SagaButton deve receber um [label]="string"' );
    }
  }

  /**
   * Define se o botão será um encaminhador ou um botão de ação
   */
  public setTypeButton() {
    if ( this.goTo !== '' ) {
      this.trueButton.nativeElement.style.display = 'none';
      this.linkButton.nativeElement.style.display = 'block';
    } else {
      this.trueButton.nativeElement.style.display = 'block';
      this.linkButton.nativeElement.style.display = 'none';
    }
  }

}



