import { CommonModule } from '@angular/common';

// Modules
import { SharedModule } from './../../shared/shared.module';
import { routing } from './../../app.routing';

// Components
import { DashboardServicosComponent } from './dashboard-servicos.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    routing
  ],
  declarations: [
    DashboardServicosComponent
  ],
  exports: [
    DashboardServicosComponent
  ]
})
export class DashboardServicosModule { }



