import { Component, OnInit, Input, ViewChild, ElementRef } from '@angular/core';

@Component({
  selector: 'app-counter-box',
  templateUrl: './counter-box.component.html',
  styleUrls: ['./counter-box.component.css']
})
export class CounterBoxComponent implements OnInit {

  @Input('type') type: string;
  @Input('message') message: string;
  @Input('money') money: number;
  @Input('meta') meta: number;
  @Input('actual') actual: number;
  @Input('unitTime') unitTime: string;

  @ViewChild('monetaryCpm') monetary: ElementRef;
  @ViewChild('timeCpm') time: ElementRef;

  constructor() { }

  ngOnInit() {
    this.checkRequired();
  }

  /**
   * Verifica os inputs obrigatórios do componente e
   * retorna no console o erro identificado.
   */
  public checkRequired() {
    if ( this.type === null ) {
      console.log('A propriedade "type" no componenent counterBox é obrigatória');
    }
  }

}



