import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { AlertBar } from 'ng2-alert-bar';

@Component({
  selector: 'app-cadastro',
  templateUrl: './cadastro.component.html',
  styleUrls: ['./cadastro.component.css']
})
export class CadastroComponent implements OnInit {

  @ViewChild('user') inputUser: ElementRef;
  @ViewChild('email') inputEmail: ElementRef;
  @ViewChild('password') inputSenha: ElementRef;

  public formulario: FormGroup;
  private email: string = "";
  private token: string = "";
  public loading: Boolean = false;
  private isValidToken: Boolean = false;

  constructor(private formBuilder: FormBuilder,
              private route: Router,
              private routeActive: ActivatedRoute,
              private http: HttpClient,
              private alert: AlertBar) { }

  ngOnInit() {
    this.email = this.routeActive.snapshot.params['email'];
    this.token = this.routeActive.snapshot.params['token'];
    this.defineForm();
    this.verifyToken();
  }

  /**
   * Define os campos e os validadores do formulário
   */
  private defineForm() {
    this.formulario = this.formBuilder.group({
      user: [ null, Validators.required  ],
      email: [ null, [ Validators.required, Validators.email ] ],
      password: [ null, Validators.required ]
    });
  }

  /**
   * Verifica os dados do formulário e caso válidos redireciona para
   * pagina de login.
   */
  public register() {
    // Visualização dos dados do formulário no console
    console.log( JSON.stringify( this.formulario.value ) );

    if (this.formulario.valid) {
      this.loading = true;
      let params = {
          email: this.email,
          token: this.token,
          password: this.formulario.get('password').value,
          nome: this.formulario.get('user').value
      };
      this.http.post("api/auth/register", params).subscribe(
        data => {
          this.loading = false;
          let dataStr = JSON.stringify(data);
          let dataJson = JSON.parse(dataStr);
          this.alert.success("", dataJson.mensagem);
          this.route.navigate(['/login']);
        },
        error => {
          this.loading = false;
          if (error.status === 422) {
            for (let msg in error.error.messages) {
              return this.alert.error("Falha ao atualizar cadastro", error.error.messages[msg]);
            }
          }
          this.alert.error("Falha ao atualizar cadastro", error.error);
        }
      );
    } else {
      this.alert.error("", "Por favor, preencha todos os campos com dados validos");
    }

  }

  private verifyToken() {
    this.loading = true;
    let params = {
      email: this.email,
      token: this.token
    }
    this.http.get('api/auth/confirm-token/verify', { params: params }).subscribe(
      data => {
        this.loading = false;
        this.isValidToken = true;
        this.formulario.patchValue({
          user: data,
          email: this.email
        });
      },
      error => {
        this.loading = false;
        this.isValidToken = false;
        if (error.status === 422) {
          for (let msg in error.error.messages) {
            return this.alert.error("Ops :)", error.error.messages[msg]);
          }
        }
        this.alert.error("Ops :)", error.error);
      }
    );
  }
}



