import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { AlertBar } from 'ng2-alert-bar';

@Component({
  selector: 'app-perfil',
  templateUrl: './perfil.component.html',
  styleUrls: ['./perfil.component.css']
})
export class PerfilComponent implements OnInit {

  @ViewChild('cargos') selectCargo: ElementRef;

  public formulario: FormGroup;
  public title: string = 'Perfil';
  public image: string = 'js/assets/pictures/ilustracao-perfil.png';
  public loading: Boolean = false;
  public usuario: any = JSON.parse(localStorage.getItem('user'));
  private codigoJogo: number;

  public works: Array<any> = [];
  public team: Array<any> = [];

  constructor(private formBuilder: FormBuilder,
              private route: Router,
              private routeActive: ActivatedRoute,
              private http: HttpClient,
              private alert: AlertBar) { }

  ngOnInit() {
    this.defineForm();
    this.codigoJogo = this.routeActive.snapshot.params['codigoJogo'];

    // na mesma regra:
    // trazer lista de setores (cargos) 
    // setar jogo corrente
    // buscar todos os jogadores do time com seus respectivos setores

    // OBS.: somente deixar atualizar cargo que ainda não esteje atribuido a ngmz
    this.getTimePerfil();
  }

  /**
   * Define os campos e validadores do formulário
   */
  private defineForm() {
    this.formulario = this.formBuilder.group({
      cargos: [ [], Validators.required ]
    });
  }

  private getTimePerfil() {
    this.loading = true;
    console.log("codigo jogo", this.codigoJogo);
    this.http.get('api/jogadores/jogos/' + this.codigoJogo + '/perfil').subscribe(
      data => {
        this.loading = false;
        let dataStr = JSON.stringify(data);
        let retorno = JSON.parse(dataStr);
        this.team = retorno.jogadores;
        this.works = retorno.cargosDisponiveis;
      },
      error => {
        this.loading = false;
        if (error.status === 422) {
          for (let msg in error.error.messages) {
            return this.alert.error("Ops :)", error.error.messages[msg]);
          }
        }
        this.alert.error("Ops :)", error.error);
      }
    );
  }

  public savePerfilJogador() {
    if (this.formulario.valid) {
      this.loading = true;
      let params = {
          codigoJogo: this.codigoJogo,
          setores: this.formulario.get('cargos').value
      };
      this.http.post('api/jogadores/jogos/perfil/save', params).subscribe(
        data => {
          this.loading = false;
          let dataStr = JSON.stringify(data);
          let retorno = JSON.parse(dataStr);

          /* jogoDefault no storage */
          localStorage.setItem('jogoDefault', JSON.stringify(retorno.jogo));

          this.alert.success("", "Cargo atualizado com sucesso!");
          this.route.navigate(['/dashboard']);
        },
        error => {
          this.loading = false;
          if (error.status === 422) {
            for (let msg in error.error.messages) {
              return this.alert.error("Ops :)", error.error.messages[msg]);
            }
          }
          this.alert.error("Ops :)", error.error);
        }
      );
    } else {
      this.alert.error("Ops :)", "Selecione uma opção para então iniciar a saga!");
    }
  }

}



