import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-financeiro-aplicacao',
  templateUrl: './financeiro-aplicacao.component.html',
  styleUrls: ['./financeiro-aplicacao.component.css']
})
export class FinanceiroAplicacaoComponent implements OnInit {

  public formInvestment: FormGroup;
  public formRescue: FormGroup;

  constructor(
    private builder: FormBuilder
  ) { }

  ngOnInit() {
    this.createForms();
  }

  /**
   * Cria as intâncias dos formulários a serem preenchidos
   */
  public createForms() {
    this.formInvestment = this.builder.group({
      loan: [null]
    });
    this.formRescue = this.builder.group({
      loan: [null]
    });
  }

  /**
   * Método chamado ao clicar no botão 'Aplicar valor'
   */
  public applyValueMethod() {
    console.log('Clique "Aplicar valor"');
  }

  /**
   * Método chamado ao clicar no botão 'Resgatar'
   */
  public rescueMethod() {
    console.log('Clique "Resgatar"');
  }

}



