import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-saga-item-list',
  templateUrl: './saga-item-list.component.html',
  styleUrls: ['./saga-item-list.component.css']
})
export class SagaItemListComponent implements OnInit {

  @Input('type') type: string;
  @Input('icon') icon: string;
  @Input('profitability') profitability: number;
  @Input('name') name: string;
  @Input('status') status: string;

  public nameButton: string;

  constructor() { }

  ngOnInit() {
    this.setNameButton();
  }

  /**
   * Define o texto dentro do botão
   */
  public setNameButton() {
    switch (this.type) {
      case 'progress' :
        this.nameButton = 'Jogar';
      break;
      case 'notStarted':
        this.nameButton = 'Iniciar';
      break;
      case 'fished':
        this.nameButton = '';
      break;
    }
  }

  /**
   * Método chamado quando o botão jogar é clicado
   */
  public playMethod() {
    console.log('Botão jogar - ', this.name, '/', this.type);
  }

}



