import { NgModule, NO_ERRORS_SCHEMA, CUSTOM_ELEMENTS_SCHEMA  } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule } from '@angular/forms';
import { AngularMultiSelectModule } from 'angular2-multiselect-dropdown';

// Modules
import { routing } from '../app.routing';

// Components
import { SagaButtonComponent } from './saga-button/saga-button.component';
import { TextfieldComponent } from './textfield/textfield.component';
import { NavComponent } from './nav/nav.component';
import { MenuItemComponent } from './menu-item/menu-item.component';
import { ProgressBarComponent } from './progress-bar/progress-bar.component';
import { ToggleButtonComponent } from './toggle-button/toggle-button.component';
import { CounterBoxComponent } from './counter-box/counter-box.component';
import { IconButtonComponent } from './icon-button/icon-button.component';
import { ModalComponent } from './modal/modal.component';
import { AddSubComponent } from './add-sub/add-sub.component';
import { DismissCardComponent } from './dismiss-card/dismiss-card.component';
import { DataComponent } from './data/data.component';
import { SimplePerfilComponent } from './simple-perfil/simple-perfil.component';
import { FileUploaderComponent } from './file-uploader/file-uploader.component';
import { InsertionCardComponent } from './insertion-card/insertion-card.component';
import { StockCardComponent } from './stock-card/stock-card.component';
import { SagaListComponent } from './saga-list/saga-list.component';
import { SagaItemListComponent } from './saga-item-list/saga-item-list.component';
import { YieldComponent } from './yield/yield.component';
import { StaticCardComponent } from './static-card/static-card.component';
import { SelectStoreComponent } from './select-store/select-store.component';

@NgModule({
  imports: [
    CommonModule,
    ReactiveFormsModule,
    AngularMultiSelectModule,
    routing,
  ],
  declarations: [
    TextfieldComponent,
    SagaButtonComponent,
    NavComponent,
    MenuItemComponent,
    ProgressBarComponent,
    ToggleButtonComponent,
    CounterBoxComponent,
    IconButtonComponent,
    ModalComponent,
    AddSubComponent,
    DismissCardComponent,
    DataComponent,
    SimplePerfilComponent,
    FileUploaderComponent,
    InsertionCardComponent,
    StockCardComponent,
    SagaListComponent,
    SagaItemListComponent,
    YieldComponent,
    StaticCardComponent,
    SelectStoreComponent
  ],
  exports: [
    TextfieldComponent,
    SagaButtonComponent,
    NavComponent,
    MenuItemComponent,
    ProgressBarComponent,
    ToggleButtonComponent,
    CounterBoxComponent,
    IconButtonComponent,
    ModalComponent,
    AddSubComponent,
    DismissCardComponent,
    DataComponent,
    SimplePerfilComponent,
    FileUploaderComponent,
    InsertionCardComponent,
    StockCardComponent,
    SagaListComponent,
    SagaItemListComponent,
    YieldComponent,
    StaticCardComponent
  ],
  schemas: [
    NO_ERRORS_SCHEMA,
    CUSTOM_ELEMENTS_SCHEMA
  ]
})
export class SharedModule { }



