import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { AlertBar } from 'ng2-alert-bar';

@Component({
  selector: 'app-recuperar-senha',
  templateUrl: './recuperar-senha.component.html',
  styleUrls: ['./recuperar-senha.component.css']
})
export class RecuperarSenhaComponent implements OnInit {

  @ViewChild('email') inputEmail: ElementRef;

  public formulario: FormGroup;
  public loading:boolean = false;

  constructor(private formBuilder: FormBuilder,
              private route: Router,
              private http: HttpClient,
              private alert: AlertBar) { }

  ngOnInit() {
    this.defineForm();
  }

  /**
   * Define os campos e validadores do formulário
   */
  private defineForm() {
    this.formulario = this.formBuilder.group({
      email: [ null, [ Validators.required, Validators.email ] ]
    });
  }

  /**
   * Verifica os dados do formulário e caso válidos redireciona para
   * pagina de login.
   */
  public recover() {
    // Visualização dos dados do formulário no console
    // @ this.formulario.value = Dados do formulário
    console.log( JSON.stringify( this.formulario.value ) );
    
    if (this.formulario.valid) {
      let params = this.formulario.value;
      this.http.post('api/auth/recuperar-senha', params).subscribe(
        data => {
					this.loading = false;
          this.route.navigate(['/recuperar-feito']);
				},
				error => {
					this.loading = false;
					if (error.status === 422) {
            for (let msg in error.error.messages) {
              return this.alert.error("Ops :)", error.error.messages[msg]);
            }
          }
          this.alert.error("Ops :)", error.error);
        }
      );
    } else {
      // Mensagem caso o formulário não seja valido
      this.alert.error("E-mail incorreto!", "Informe um endereço de e-mail válido.");
    }

  }

}



