import { Component, OnInit, Input, ViewChild, ElementRef, asNativeElements } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { AlertBar } from 'ng2-alert-bar';

import { AuthenticationService } from '../../services/index';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  @ViewChild('email') inputEmail: ElementRef;
  @ViewChild('password') inputSenha: ElementRef;

  public formulario: FormGroup;
  public returnUrl: string;

  constructor(private formBuilder: FormBuilder,
              private route: Router,
              private activatedRoute: ActivatedRoute,
              private alert: AlertBar,
              private authenticationService: AuthenticationService) { }

  ngOnInit() {
    this.defineForm();

    this.authenticationService.logout()
			.subscribe(
				data => {
					localStorage.clear();
					// page default...
					this.returnUrl = this.activatedRoute.snapshot.queryParams['returnUrl'] || '/';
				},
				error => {
					this.alert.error("Erro ao realizar logout", error.message);
				});
  }

  /**
   * Define os campos e validadores do formulário
   */
  private defineForm() {
    this.formulario = this.formBuilder.group({
      email: [ null, [ Validators.required, Validators.email ] ],
      password: [ null, Validators.required ]
    });
  }

  /**
   * Verifica os dados do formulário e caso válidos redireciona para
   * pagina de recursos-humanos.
   */
  public login() {
    // @ this.formulario.value = Dados do formulário
    if (this.formulario.valid) {
      this.authenticationService.login(this.formulario.value)
          .subscribe(
              data => {
                console.log("data: ", data);

                let redirect: string = this.returnUrl;
                let user = data.user;

                /* atualizando dados do usuario no storage */
                localStorage.setItem('user', JSON.stringify(user));

                /* para perfil jogador */
                if (user.jogador === 1) {
                  let jogoCorrente = user.perfil.jogo_corrente;
                  // atualiza o ultimo jogo acessado pelo jogador
                  localStorage.setItem('jogoDefault', JSON.stringify(jogoCorrente));
                  let jogos = data.perfilJogador.jogos;
                  // atualiza a lista de todos os jogos ativos do jogador
                  localStorage.setItem('jogos', JSON.stringify(jogos));
                  if ((jogoCorrente === null || data.perfilJogador.primeiro_acesso_jogo) && jogos.length > 0) {
                    // primeiro acesso ao jogo devemos direcionar para atualização do cargo no jogo
                    let codigoJogo = jogoCorrente !== null ? jogoCorrente.codigo : jogos[0].codigo;
                    redirect = '/perfil/' + codigoJogo;
                  }
                }

                this.route.navigate([redirect]);
              },
              error => {
                if (error.status === 422) {
                  for (let msg in error.error.messages) {
                    return this.alert.error("Falha ao realizar login", error.error.messages[msg]);
                  }
                }
                this.alert.error("Falha ao realizar login", error.error);
              }

          );
    } else {
      // Mensagem caso o formulário não seja valido
      this.alert.error("Dados inválidos!", "Digite um e-mail e senha válidos.");
    }

  }

}



