import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-insertion-card',
  templateUrl: './insertion-card.component.html',
  styleUrls: ['./insertion-card.component.css']
})
export class InsertionCardComponent implements OnInit {

  @Input('value') value: number;
  @Input('reach') reach: number;
  @Input('insertion') insertion: string;
  @Input('icon') icon: string;
  @Input('data') data: any;

  public modalIsVisible = false;

  constructor() { }

  ngOnInit() {
  }

  /**
   * Mostra o modal na tela
   */
  public openModal() {
    this.modalIsVisible = true;
  }

  /**
   * Esconde o modal
   * @param $event Objeto retornado quando o modal é fechado
   */
  public closeModal($event) {
    if ( $event.state === 'close' ) {
      this.modalIsVisible = false;
    }
  }

}



