import { Component, OnInit, Input, ViewChild, ElementRef, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-dismiss-card',
  templateUrl: './dismiss-card.component.html',
  styleUrls: ['./dismiss-card.component.css']
})
export class DismissCardComponent implements OnInit {

  @Input('id') id: number;
  @Input('avatar') avatar: string;
  @Input('name') name: string;
  @Input('enterDate') enterDate: string;
  @Input('recisao') recisao: number;

  @ViewChild('checkItem') checkItem: ElementRef;

  @Output() changeState = new EventEmitter();

  public dateLiteral: string;

  constructor() { }

  ngOnInit() {
    this.dateLiteral = this.formatTimeLiteral(this.enterDate);
  }

  /**
   * Emite um evento ao component pai informando o status do component
   * {
   *  state: boolean, // Estado do component
   *  prof_id: number // Id do profissinal (des)selecionado
   * }
   */
  public isChange() {
    if (this.checkItem.nativeElement.checked) {
      this.changeState.emit({
        state: true,
        prof_id: this.id
      });
    } else {
      this.changeState.emit({
        state: false,
        prof_id: this.id
      });
    }
  }

  /**
   * Formata a entrada e retorna sua forma escrita
   * @param input Data no formato padrão de DB
   */
  public formatTimeLiteral(input: string) {
    let literalTime = '';

    const myDate = new Date(input);
    const today = new Date();

    const timeDiff = Math.abs(today.getTime() - myDate.getTime());
    const diffDays = Math.ceil(timeDiff / (1000 * 3600 * 24));

    if ( diffDays > 365 ) {
      const year = Math.floor( diffDays / 365 );
      if ( (diffDays % 365) < 30 ) {
        const days = diffDays - ( year * 365 );
        literalTime = `${year} anos e ${days} dias`;
      } else {
        const month = Math.floor( ( diffDays % 365 ) / 30 );
        literalTime = `${year} anos e ${month} meses`;
      }
    } else {
      const month = Math.floor( diffDays / 30 );
      const days = diffDays % 30 ;
      literalTime = `${month} meses e ${days} dias`;
    }

    return literalTime;
  }


}



