import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

// Modules
import { NgDatepickerModule } from 'ng2-datepicker';

// Components
import { DatePickerComponent } from './date-picker.component';

@NgModule({
  imports: [
    CommonModule,
    NgDatepickerModule
  ],
  declarations: [
    DatePickerComponent
  ],
  exports: [
    DatePickerComponent
  ]
})
export class DatePickerModule { }



