import { Component, OnInit, Input, ElementRef } from '@angular/core';

@Component({
  selector: 'app-static-card',
  templateUrl: './static-card.component.html',
  styleUrls: ['./static-card.component.css']
})
export class StaticCardComponent implements OnInit {

  @Input('icon') icon: string;
  @Input('label') label: string;
  @Input('value') value: string;
  @Input('frequency') frequency: number;
  @Input('inBox') inBox = true;
  // Types: 'money', 'percent' e 'quantity'
  @Input('type') type: string;
  // Sizes: 'lg' e 'md'
  @Input('size') size = 'lg';
  // Status: 'danger', 'attention' e 'success'
  @Input('status') status = 'success';

  constructor(public elRef: ElementRef) { }

  ngOnInit() {
  }

}



