import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-add-sub',
  templateUrl: './add-sub.component.html',
  styleUrls: ['./add-sub.component.css']
})
export class AddSubComponent implements OnInit {

  @Input('start') start: number;
  @Input('size') size: string;

  @Output() actualValue = new EventEmitter();

  constructor() { }

  ngOnInit() {
  }

  /**
   * Emite o valor atual do contador
   */
  public emitValue() {
    this.actualValue.emit({
      value: this.start
    });
  }

  /**
   * Adiciona uma unidade ao contador
   */
  public add() {
    this.start += 1;
    this.emitValue();
  }

  /**
   * Subtrai uma unidade ao contador
   */
  public sub() {
    if ( this.start !== 1) {
      this.start -= 1;
      this.emitValue();
    }
  }

}



