import { NgModule, CUSTOM_ELEMENTS_SCHEMA, NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';

// Modules
import { SliderCardModule } from '../slider-card/slider-card.module';
import { NgxCarouselModule } from 'ngx-carousel';
import 'hammerjs';

// Componets
import { ServicosClientesComponent } from './servicos-clientes.component';

@NgModule({
  imports: [
    CommonModule,
    SliderCardModule,
    NgxCarouselModule
  ],
  declarations: [
    ServicosClientesComponent
  ],
  exports: [
    ServicosClientesComponent
  ],
  schemas: [
    NO_ERRORS_SCHEMA,
    CUSTOM_ELEMENTS_SCHEMA
  ]
})
export class ServicosClientesModule { }



