import { Component, OnInit, Input, EventEmitter, ViewChild, ElementRef, Output } from '@angular/core';

@Component({
  selector: 'app-toggle-button',
  templateUrl: './toggle-button.component.html',
  styleUrls: ['./toggle-button.component.css']
})
export class ToggleButtonComponent implements OnInit {

  @Input('left') left: string;
  @Input('right') right: string;

  @ViewChild('checkLeft') checkLeft: ElementRef;
  @ViewChild('checkRight') checkRight: ElementRef;

  @Output() selectedOption = new EventEmitter();

  constructor() { }

  ngOnInit() {
    this.seeChange();
  }

  /**
   * Emite um evento idicando a mudança de estado do botão
   */
  public seeChange() {
    const left = this.checkLeft.nativeElement.checked;
    const right = this.checkRight.nativeElement.checked;

    if (left) {
      this.selectedOption.emit({
        selected: this.left
      });
    } else {
      if (right) {
        this.selectedOption.emit({
          selected: this.right
        });
      }
    }

  }


}



