import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-card-professional',
  templateUrl: './card-professional.component.html',
  styleUrls: ['./card-professional.component.css']
})
export class CardProfessionalComponent implements OnInit {

  @Input('id') id: number;
  @Input('work') work: string;
  @Input('quantity') quantity: number;
  @Input('value') value: number;
  @Input('production') production: any;
  @Input('avatar') avatar: string;

  @Output() hireButtonClicked = new EventEmitter();
  @Output() dismissButtonClicked = new EventEmitter();

  constructor() { }

  ngOnInit() {
  }

  /**
   * Evento emitido ao clicar no botão 'Contratar'
   */
  public hireButton() {
    this.hireButtonClicked.emit({
      id: this.id,
      state: true,
      button: 'hire'
    });
  }

  /**
   * Evento emitido ao clicar no botão 'Demitir'
   */
  public dismissButton() {
    this.dismissButtonClicked.emit({
      id: this.id,
      state: true,
      button: 'dismiss'
    });
  }

}



