import { Component, OnInit, Input, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';

@Component({
  selector: 'app-select-store',
  templateUrl: './select-store.component.html',
  styleUrls: ['./select-store.component.css']
})
export class SelectStoreComponent implements OnInit {

  @Input('id') id: number;
  @Input('logo') logo: string;
  @Input('name') name: string;
  @Input('quantity') quantity: number;
  @Input('unitPrice') unitPrice: number;
  @Input('sendTime') sendTime: string;

  @ViewChild('inputCheck') inputCheck: ElementRef;

  @Output() isChecked = new EventEmitter();

  public total: number;

  constructor() { }

  ngOnInit() {
    this.calculateTotal();
  }

  /**
   * Calcula o valor total em realação a quantidade e
   * ao preço unitário do produto
   */
  public calculateTotal() {
    this.total = this.quantity * this.unitPrice;
  }

  /**
   * Quando o status do input for checked é emitido
   * um evento para aplicação informando o 'id' do checkBox selecionado
   */
  public listenerChecked() {
    if ( this.inputCheck.nativeElement.checked ) {
      this.isChecked.emit(this.id);
    }
  }

}



