import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-dashboard-servicos',
  templateUrl: './dashboard-servicos.component.html',
  styleUrls: ['./dashboard-servicos.component.css']
})
export class DashboardServicosComponent implements OnInit {

  public responsible = 'Josiane Raquel';
  public arrayServices: Array<any> = [
    { name: 'Aplciativo Mobile', icon: 'js/assets/icons/icon-mobile-line.svg', status: 'success' },
    { name: 'Aplciativo Mobile', icon: 'js/assets/icons/icon-mobile-line.svg', status: 'danger' }
  ];

  constructor() { }

  ngOnInit() {
    this.defineStatus();
  }

  /**
   * Define as variáveis de controle da status bar
   */
  public defineStatus() {
    for ( const item of this.arrayServices ) {
      switch ( item.status ) {
        case 'success':
          item.statusDesc = 'Pronto para entrega';
          item.buttonText = 'Entregar';
          item.percent = 100;
        break;
        case 'neutral':
          item.statusDesc = '';
          item.buttonText = 'Ver';
          item.percent = 67;
        break;
        case 'danger':
          item.statusDesc = 'Em atraso';
          item.buttonText = 'Ver';
          item.percent = 47;
        break;
      }
    }
  }

  /**
   * Método chamado quando o botão da lista é clicado
   * @param nameButton Nome do botão clicado
   */
  public clickOnButton(nameButton) {
    console.log('Clique no botão - ' + nameButton);
  }

}



