import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

// Modules
import { SharedModule } from '../../shared/shared.module';
import { MainHeaderModule } from '../../components/main-header/main-header.module';
import { MainFooterModule } from '../../components/main-footer/main-footer.module';
import { DashboardGeralModule } from './../../components/dashboard-geral/dashboard-geral.module';
import { DashboardFinanceiroModule } from './../../components/dashboard-financeiro/dashboard-financeiro.module';
import { DashboardRecursosHumanosModule } from './../../components/dashboard-recursos-humanos/dashboard-recursos-humanos.module';
import { DashboardServicosModule } from '../../components/dashboard-servicos/dashboard-servicos.module';
import { DashboardComprasModule } from './../../components/dashboard-compras/dashboard-compras.module';
import { DashboardMarketingModule } from '../../components/dashboard-marketing/dashboard-marketing.module';

// Components
import { DashboardComponent } from './dashboard.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    MainHeaderModule,
    MainFooterModule,
    DashboardGeralModule,
    DashboardFinanceiroModule,
    DashboardRecursosHumanosModule,
    DashboardServicosModule,
    DashboardComprasModule,
    DashboardMarketingModule
  ],
  declarations: [
    DashboardComponent
  ],
  exports: [
    DashboardComponent
  ]
})
export class DashboardModule { }



