import { Component, OnInit, Input, HostListener } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { AlertBar } from 'ng2-alert-bar';

@Component({
  selector: 'app-dropdown-enterprise',
  templateUrl: './dropdown-enterprise.component.html',
  styleUrls: ['./dropdown-enterprise.component.css']
})
export class DropdownEnterpriseComponent implements OnInit {

  @Input('img') img: string;
  @Input('name') name: string;
  @Input('subtitle') subtitle: string;
  @Input('options') options: Array<any>;

  public loading = false;
  public actived = false;
  public isNaturalAction = false;
  public isDropdownOption = false;

  constructor(private route: Router,
              private http: HttpClient,
              private alert: AlertBar) { }

  ngOnInit() {
  }

  /**
   * Seta a propriedade ativa e inativa do dropdown
   */
  public openDropdown() {
    this.isNaturalAction = true;
    if ( this.actived ) {
      this.actived = false;
    } else {
      this.actived = true;
      setTimeout(() => {
        this.isNaturalAction = false;
      }, 100);
    }
  }

  /**
   * Evita que o listner de eventos o escute e redireciona
   * a aplicação para url inserida.
   * @param item Jogo representa jogo/empresa que foi selecionado pelo usuário
   */
  public isOneDropdownOption(item: any) {
    this.isDropdownOption = true;

    /* atualizando perfil no backend */
    let params = this.updateJogoCorrenteInPerfil(item);
    this.http.post('api/user/perfil/save', params).subscribe(
      data => {
        this.loading = false;
        this.actived = false;
        let dataStr = JSON.stringify(data);
        let retorno = JSON.parse(dataStr);

        let redirect: string = "/dashboard"; 
        if (retorno.isPrimeiroAcessoJogo) {
          redirect = "/perfil/" + item.codigo; 
        } else {
          /* jogoDefault no storage */
          localStorage.setItem('jogoDefault', JSON.stringify(item));

          /* atualizando labels */
          this.name = item.nome_jogo;
          this.subtitle = item.subtitulo;
        }
        
        /* atualizar dashboard com os dados do jogo selecionado (regra no dashboard) */
        this.route.navigate([redirect]);
      },
      error => {
        this.loading = false;
        if (error.status === 422) {
          for (let msg in error.error.messages) {
            return this.alert.error("Ops :)", error.error.messages[msg]);
          }
        }
        this.alert.error(" Ops :)", error.error);
      }
    );
  }

  /**
   * Escuta qualquer envento dentro da interface e caso exitir algum click
   * o método é executado.
   * Define a variável de ativação do dropdown como falso
   */
  @HostListener('document:click', ['$event.target'])
  public closeDropdown() {
    if ( !this.isNaturalAction && this.isDropdownOption === false) {
      this.actived = false;
    }
  }

  /*
   * Atualiza novo jogo selecionado no perfil do usuario
   */
  private updateJogoCorrenteInPerfil(item: any) {
    let user = JSON.parse(localStorage.getItem('user'));
    let perfil = user.perfil;
    perfil.jogo_corrente_codigo = item.codigo;
    return perfil;
  }

}



